 * name: SeparateAccountManage
 * description:
 * author: roct
 * date: 22:04 2021/5/25 0025
 */
import { Component, Vue } from 'vue-property-decorator'
import { getTransPages, getTransDetail } from '@/api/sepAccount/sepAccount'
@Component({
  components: {},
})
export default class SeparateAccountManage extends Vue {
  private pageTitle = '分账账户管理'
  private query = {
    current: 1,
    size: 10,
    total: 0,
  }
  private list: any[] = []
  public mounted(): void {
    this.getTransList()
  }
  /**
   * @Author roct
   * @Description 修改当前页码
   * @Date 21:27 2021/5/26 0026
   **/
  private handleCurrentChange(e: number) {
    this.query.current = e
  }
  /***
   * @Author roct
   * @Description 获取分账户列表
   * @Date 21:26 2021/5/26 0026
   **/
  private async getTransList() {
    const res = await getTransPages(this.query)
    this.list = res.data.records
    this.query.total = res.data.total
  }
  /**
   * @Author roct
   * @Description 前往详情页面
   * @Date 22:38 2021/5/26 0026
   **/
  private seeClick(scope: any) {
    Vue.ls.set('/manage/sys/SepAccountDetail', scope.row.id)
    location.href = '/index.html/#/manage/sys/SepAccountDetail'
  }
  /**
   * @Author roct
   * @Description 前往设置页面
   * @Date 21:28 2021/5/26 0026
   **/
  private settingClick(scope: any) {
    Vue.ls.set('/manage/sys/SettingSeparateAccount', scope.row.id)
    location.href = '/index.html/#/manage/sys/SettingSeparateAccount'
  }
}
